<template>
    <view class="applet">
        <view class="applet-label" @click="show = !show">
            <i class="w_editor-icon w_editor-icon-xiaochengxu"></i>
            {{data.title}}
        </view>
        <WPopup :show="show" @confirm="handleConfirm" @cancel="cancel">
            <template slot="title">
                添加跳转小程序
            </template>
            <view class="applet-form">
                <span class="applet-form__label">标题</span>
                <u-input v-model="data.title" type="text" placeholder="可输入小程序名称"/>
                <span class="applet-form__label">描述</span>
                <u-input v-model="data.description" type="text" placeholder="不填不显示(选填)"/>
                <span class="applet-form__label">小程序APPID</span>
                <u-input v-model="data.appid" type="text" placeholder="点此输入"/>
                <span class="applet-form__label">小程序页面路径</span>
                <u-input v-model="data.path" type="text" placeholder="如: pages/index"/>
                <span class="applet-form__label">小程序原始ID</span>
                <u-input v-model="data.originalid" type="text" placeholder="小程序对应的以gh_开头的ID(选填)"/>
            </view>
            <u-toast ref="uToast"></u-toast>
        </WPopup>
    </view>
</template>

<script>
    import WPopup from '../components/w_popup/index.vue'
    export default {
        components: {
            WPopup
        },
        props: {
            value: {
              type: [String, Number, Object, Array, Boolean],
              default: undefined,
            },
            option: {
                type: Object,
                default: {},
            },
        },
        data() {
            return {
				data: {},
                show: true,
                opened: false,
            }
        },
        watch: {
            data: {
                deep: true,
                handler(e) {
                    this.$emit('input', e)
                },
            },
            value: {
                deep: true,
                immediate: true,
                handler(e) {
                    if(e){
                        this.data = e
                    }else{
                        this.data = {
                            title: '',
                            description: '',
                            appid: '',
                            path: '',
                            originalid: ''
                        }
                    }
                },
            },
        },
        methods: {
            cancel() {
                this.show = false
                if(!this.opened){
                    this.$emit('permit', false)
                }
            },
            handleConfirm() {
                if(this.data.title){
                    this.show = false
                    this.opened = true
                    this.$emit('permit', true)
                }else{
                    this.$refs.uToast.show({message: '标题不能为空'})
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    
    
    .applet{
        padding: 30rpx;
        &-form{
            width: 500rpx;
            margin-bottom: 20rpx;
            &__label{
                display: block;
                margin-top: 20rpx;
            }
        }
        &-label{
            color: #666;
            i{
                margin-right: 10rpx;
            }
        }
    }
</style>